<template>
  <!-- 更改密码 -->
  <div class="change-body">
    <div class="change-section">
      <p>更改密码</p>
      <a-form
        id="components-form-demo-normal-login"
        :form="form"
        class="login-form"
        @submit="handleSubmit"
      >
        <a-form-item>
          <a-input
            v-decorator="[
              'userName',
              {
                rules: [
                  {
                    required: true,
                    pattern: /^(?=.*([a-zA-Z].*))(?=.*[0-9].*)[a-zA-Z0-9-*/+.~!@#$%^&*()]{8,20}/,
                    message: '原始密码不能为空',
                  },
                ],
              },
            ]"
            type="password"
            placeholder="请输入原始密码"
          >
          </a-input>
        </a-form-item>
        <a-form-item>
          <a-input
            v-decorator="[
              'password',
              {
                rules: [
                  {
                    required: true,
                    pattern: /^(?=.*([a-zA-Z].*))(?=.*[0-9].*)[a-zA-Z0-9-*/+.~!@#$%^&*()]{8,20}/,
                    message: '新密码格式不对',
                  },
                ],
              },
            ]"
            type="password"
            placeholder="请输入新密码，包含数字与字母"
          >
          </a-input>
        </a-form-item>
        <a-form-item>
          <a-input
            v-decorator="[
              'passwor',
              {
                rules: [{ required: true, message: '密码不能为空' }],
              },
            ]"
            type="password"
            placeholder="请再次输入新密码"
          >
          </a-input>
        </a-form-item>
        <a-form-item>
          <a-button type="primary" html-type="submit" class="login-form-button">
            确认修改
          </a-button>
        </a-form-item>
      </a-form>
    </div>
  </div>
</template>

<script>
export default {
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: "normal_login" });
  },
  data() {
    return {};
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields(async (err, values) => {
        if (values.password != values.passwor) {
          this.$message.error("两次密码不一样");
          values.passwor = [];
        }
        let str = {
          id: sessionStorage.getItem("id"),
          newPwd: this.$md5(values.password),
          pwd: this.$md5(values.userName),
        };
        let { data } = await this.$http.update(str);
        if (data.code == "0000") {
          this.$message.success("更改密码成功下次登录请使用新密码");
        }
      });
    },
  },
};
</script>

<style scoped>
#components-form-demo-normal-login .login-form-button {
  width: 100%;
}
.change-section {
  width: 500px;
  margin: 0 auto;
  padding-top: 50px;
}
.change-section > :nth-child(1) {
  font-size: 18px;
}
</style>